.cv {
    section {
        margin: 60px 0;
        display: block;
        overflow: hidden;

        h1 {
            margin: 0;
            font-size: 36px;
            font-weight: 400
        }

        h2 {
            font-size: 20px;
            font-weight: 700;
            text-transform: uppercase
        }

        p,
        h2 {
            margin: 0;
            padding: 0;
        }

        hr {
            border-top: 1px dashed #e0e0e0;
            margin: 30px 0 15px;

            &:before {
                width: 0;
                height: 0;
                margin: 0 0;
            }

            &:after {
                width: 0;
                height: 0;
                margin: 0 0;
            }
        }
    }
    .col-1 {
        width: 260px;
        float: left;
    }
    .col-2 {
        width: 700px;
        float: right;
    }
}

@media print {
    body {
        color:#000000 !important;
        font-size: 20px !important;
    }

    a {
        color:#000000 !important;
        border-bottom: 0 !important;
    }

    nav,
    .btn {
        display: none;
    }

    section {
        margin: 20px 0 !important;

        .col-1 {
            width: 21% !important;
        }
        .col-2 {
            width: 79% !important;
        }
    }

    hr {
        margin: 10px 0 5px !important;
    }



}