@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700|Playfair+Display);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
  overflow: hidden;
  margin: 60px 0 0 0;
  display: block;
  width: 100%;
  border-top: 1px solid #ffffff;
  box-shadow: inset 0px -2px 0px 0px white; }
  nav ul {
    margin: 0;
    padding: 0;
    list-style: none; }
    nav ul li {
      float: left; }
      nav ul li a {
        display: inline-block;
        padding: 20px;
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 600;
        color: #ffffff;
        border-bottom: 2px solid #ffffff; }
        nav ul li a:hover {
          background: rgba(255, 255, 255, 0.1); }
      nav ul li .active {
        color: #ffffff;
        border-bottom: 2px solid #ad5389; }

.cv section {
  margin: 60px 0;
  display: block;
  overflow: hidden; }
  .cv section h1 {
    margin: 0;
    font-size: 36px;
    font-weight: 400; }
  .cv section h2 {
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase; }
  .cv section p,
  .cv section h2 {
    margin: 0;
    padding: 0; }
  .cv section hr {
    border-top: 1px dashed #e0e0e0;
    margin: 30px 0 15px; }
    .cv section hr:before {
      width: 0;
      height: 0;
      margin: 0 0; }
    .cv section hr:after {
      width: 0;
      height: 0;
      margin: 0 0; }

.cv .col-1 {
  width: 260px;
  float: left; }

.cv .col-2 {
  width: 700px;
  float: right; }

@media print {
  body {
    color: #000000 !important;
    font-size: 20px !important; }
  a {
    color: #000000 !important;
    border-bottom: 0 !important; }
  nav,
  .btn {
    display: none; }
  section {
    margin: 20px 0 !important; }
    section .col-1 {
      width: 21% !important; }
    section .col-2 {
      width: 79% !important; }
  hr {
    margin: 10px 0 5px !important; } }

body {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  color: #e0e0e0;
  background: #ad5389;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3c1053, #ad5389);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

h1 {
  font-family: 'Playfair Display', serif; }

.wrap {
  margin: auto;
  width: 960px; }

.container {
  margin: 60px 0 0 0; }

p {
  line-height: 150%; }

a {
  display: inline-block;
  color: #e0e0e0;
  text-decoration: none;
  border-bottom: 2px solid #3c1053; }
  a:hover {
    border-bottom: 2px solid #e0e0e0; }

hr {
  margin: 60px 0;
  text-align: center;
  border: none; }
  hr:before {
    content: '';
    display: inline-block;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #eee;
    margin: 0 0.4em; }
  hr:after {
    content: '';
    display: inline-block;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #eee;
    margin: 0 0.4em; }

.btn {
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 300;
  text-transform: uppercase;
  color: #ffffff;
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: none; }

ul.list {
  list-style: none; }
  ul.list li {
    padding: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    font-size: 20px;
    font-weight: 300;
    text-transform: uppercase; }
    ul.list li:hover {
      background: rgba(255, 255, 255, 0.1); }

.e-paragraph {
  margin: 0;
  padding: 0;
  font-size: 36px;
  font-family: 'Playfair Display', serif;
  text-align: center; }

.profile-pic {
  display: block;
  margin: auto;
  margin-bottom: 60px;
  width: 300px;
  border-radius: 50%;
  box-shadow: 5px 5px 0px 0px rgba(255, 255, 255, 0.5); }

