@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700|Playfair+Display');

body { 
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  color: #e0e0e0;
  background: #ad5389;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #3c1053, #ad5389);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #3c1053, #ad5389); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

h1 {
  font-family: 'Playfair Display', serif;
}

.wrap {
  margin: auto;
  width: 960px;
}

.container {
  margin: 60px 0 0 0;
}

p {
  line-height: 150%;
}

a {
  display: inline-block;
  color:#e0e0e0;
  text-decoration: none;
  border-bottom: 2px solid #3c1053;

  &:hover {
    border-bottom: 2px solid #e0e0e0;
  }
}

hr {
  margin: 60px 0;
  text-align: center;
  border: none;

  &:before {
    content: '';
    display: inline-block;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #eee;
    margin: 0 0.4em;
  }

  &:after {
    content: '';
    display: inline-block;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #eee;
    margin: 0 0.4em;
  }
}

.btn {
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 300;
  text-transform: uppercase;
  color: #ffffff;
  border: 2px solid rgba(255,255,255,0.5);
  background: none
}

ul.list {
  list-style: none;

  li {
    padding: 20px;
    border-bottom: 1px solid rgba(255,255,255,0.5); 
    font-size: 20px;
    font-weight: 300;
    text-transform: uppercase;

    &:hover {
      background: rgba(255,255,255,0.1);
    }
  }
}

.e-paragraph {
  margin: 0;
  padding: 0;
  font-size: 36px;
  font-family: 'Playfair Display', serif;
  text-align: center;
}

.profile-pic {
  display: block;
  margin: auto;
  margin-bottom: 60px;
  width: 300px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-box-shadow: 5px 5px 0px 0px rgba(255,255,255,0.5);
-moz-box-shadow: 5px 5px 0px 0px rgba(255,255,255,0.5);
box-shadow: 5px 5px 0px 0px rgba(255,255,255,0.5);
}